<template>
    <div >
        <div>
            <h3>变量作用域</h3>
            <ul>
                <li>全局变量：在全局环境中用 var 关键字声明。如果不带 var，隐式声明默认是一个全局变量。</li>
                <li>函数作用域内变量：局部变量，外部无法访问，可以通过<strong>闭包</strong>获得。</li>
                <li>立即执行函数内变量：函数作用域。</li>
                <li>ES6后使用 let 关键字形成块级作用域，在最近的{}范围内部。</li>
            </ul>
        </div>

        <h3>变量提升 Hoisting </h3>
        <ul>
            <li>JavaScript 会提升变量，将 var 声明提到最前端，但不会赋值。</li>
            <li>函数也有这种待遇，且函数会被赋值。</li>
            <li>如果函数声明与函数表达式同时出现，<strong>函数声明</strong>优先于表达式。<a href="https://www.cnblogs.com/liuhe688/p/5891273.html">具体例子可参考这里。</a></li>
        </ul>
        
        <div class="code" >
            <code>
            function a (){
                console.log(b)
                var b = 1
                function b() {console.log(2)}

            }
            a() // b() {console.log(2)}
            </code>
        </div>
        <p>等同于：</p>
        <div class="code">
            <code>
            function a (){
                var b
                function b() {console.log(2)}
                console.log(b)
                b = 1
            }
            a() // b() {console.log(2)}
            </code>
        </div>


        <h3>闭包</h3>
        <p>可以访问到某函数内部变量的函数。</p>
        <div class="code">
            <code>
            function f1() {
                var a = 1
                function f2() {
                    return a
                }
                return f2
            }
            var t = f1()
            t() //  1
            </code>
        </div>

        <h3>执行上下文</h3>
        <p><a href="https://github.com/mqyqingfeng/Blog/issues/8">参考代码和详解</a></p>
        <p>执行上下文可以解释如何通过闭包访问函数内部变量。</p>
        <p>运行代码时会根据作用域创建不同的执行上下文，也就是目前代码所处的一种<strong>环境</strong>。执行上下文会被保存在栈中。</p>
        <div class="code">
            <code>
            var scope = "global scope";
            function checkscope(){
                var scope = "local scope";
                function f(){
                    return scope;
                }
                return f();
            }
            checkscope();
            </code>
        </div>
        <p>例如以上代码中存在
            <strong>全局</strong>、
            <strong>checkscope</strong>、
            <strong>f</strong>三个不同的执行上下文。
        </p>
        <p>一个在全局中定义的函数，在函数初始化的时候会将全局执行上下文（也就是上一层环境）放到 [[scope]] 这个属性中。</p>
        <p>之后函数的执行上下文初始化做这四件事：</p>
        <ul>
            <li>复制函数 [[scope]] 属性创建作用域链</li>
            <li>用 arguments 创建活动对象</li>
            <li>初始化活动对象，即加入形参、函数声明、变量声明</li>
            <li>将活动对象压入 checkscope 作用域链顶端</li>
        </ul>
        <p>调用 f() 函数时，未能找到变量，则通过作用域链到上一级的执行上下文中去寻找变量。</p>
    </div>
</template>

