<template>
    <div id="">
        <div>
            <select name="" id="select" @change="changeOption">
                <option value="transform">转换 transform</option>
                <option value="transition">过渡 transition</option>
                <option value="loading">加载 loading</option>
                <option value="canvas">绘制 canvas</option>
            </select>
        </div>
        <div id="transition" v-show="transition">
            <div>
                <h4>按钮</h4>
                <button>Button</button>
            </div>
            <div>
                <h4>手风琴效果</h4>
                <ul id="list">
                    <li class="list-item" v-for="list in lists" v-bind:key="list.id" @click="changeList">{{ list.index }}</li>
                </ul>
            </div>
        </div>    
        <div id="loading" v-show="loading">
            <div>
                <h4>Loading A</h4>
                <div class="parant">
                    <div class="loading-one"></div>
                </div>
                <div class="parant">
                    <div class="loading-two"></div>
                </div>
                <div class="parant">
                    <div class="loading-three"></div>
                </div>

                <h4>Loading B</h4>
                <div class="loading-four">
                    <div class="dot-one"></div>
                    <div class="dot-two"></div>
                    <div class="dot-three"></div>
                    <div class="dot-four"></div>
                </div>

                <div class="loading-five"> 
                    <div class="white-circle"></div>
                </div>
                
            </div>
        </div>
        <div id="transform" v-show="transform">     
            <div class="part">
                <h4>2d 转换</h4>
                <h5>球体掉落</h5>
                <div class="ball" ></div>
                <h5 style="margin-top: 130px;">旋转效果</h5>
                <div class="rotate" ></div>
                <h5>缩放效果</h5>
                <div class="heart"></div>
            </div>
            <div class="part">
                <h4>3d 转换</h4>
                <p><a href="https://www.zhangxinxu.com/wordpress/2012/09/css3-3d-transform-perspective-animate-transition/" target="_blank">参考文章</a></p>
                <p>分为三层，stage、inner 和图片。stage 设置透视，通过控制 inner 旋转实现图片切换，图片通过绝对定位聚于一点，（360/图片张数）来设置旋转角度之间的差值。最后用 translateZ 实现图片之间的分离，translateZ = (width/2) * tan(img_num/360) + img_margin</p>
                <div class="stage">
                    <div class="inner" style="transform:rotateY(0deg);" @click="changePict">
                        <img src="" alt="" class="img">
                        <img src="" alt="" class="img">
                        <img src="" alt="" class="img">
                        <img src="" alt="" class="img">
                        <img src="" alt="" class="img">
                        <img src="" alt="" class="img">
                    </div>
                </div>
            </div>
        </div>
        <div id="canvas_part" v-show="canvas">
            <canvas id="canvas" width="600" height="300"></canvas>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Animation',
    data() {
        return {
            transition: false,
            loading: false,
            transform: true,
            canvas: false,
            lists: [
                { index: '' },
                { index: '' },
                { index: '' }]
        }
    },
    mounted() {
        function getRandomInt (min, max) {
            return Math.floor(Math.random() * (max - min) + min)
        }
        
        function Ball() {}

        Ball.prototype = {
            x: 100,
            y: 100,
            radius: 10,
            vx: 3,
            vy: 5,
            color: '#76b39d',
            draw: function () {
                ctx.beginPath();
                ctx.arc(this.x, this.y, this.radius, 0, Math.PI * 2, true);
                ctx.closePath();
                ctx.fillStyle = this.color;
                ctx.fill();
            },
            run: function () {
                this.x += this.vx
                this.y += this.vy
            }
        }

        let canvas = document.getElementById('canvas')
        let ctx = canvas.getContext('2d')
        let colorList = ['#ff9090', '#ea8a8a', '#ffe5ab', 
                        '#feb062','#89a4c7', '#f2c6b4', 
                        '#bfd8d5', '#fed9ca', '#dbe9b7']
        let ballList = []
        let ballNumber = 10
      
        for (let i = 0; i < ballNumber; i++) {
            let b = new Ball()
            b.radius = getRandomInt(10,20)
            b.x = getRandomInt(1, 600)
            b.y = getRandomInt(1, 300)
            b.vx = getRandomInt(1, 5)
            b.vy = getRandomInt(1, 5)
            b.color = colorList[getRandomInt(0, colorList.length)]
            b.draw()
            ballList.push(b)
        }

        function ballRun() {
            ctx.clearRect(0,0, canvas.width, canvas.height)
            let l = ballList.length
            let h = canvas.height
            let w = canvas.width
            for (let i = 0; i < l; i++) {
                let cur = ballList[i]
                if(cur.y > h - cur.radius || cur.y < cur.radius){
                    cur.vy *= -1
                }
                if(cur.x > w - cur.radius || cur.x < cur.radius){
                    cur.vx *= -1 
                }         
                
                cur.run()
                cur.draw()
            }
            window.requestAnimationFrame(ballRun)
        }
        ballRun()

        
        
    },
    methods: {
        clear: function () {
            this.transition = false
            this.loading = false
            this.transform = false
            this.canvas = false
        },
        changeOption: function () {
            let select = document.getElementById('select')
            let index = select.selectedIndex
            let value = select.options[index].value
            this.clear()
        
            if (value == 'transition') {
                this.transition = true
            } else if (value == 'loading') {
                this.loading = true
            } else if (value == 'transform') {
                this.transform = true
            } else if (value == 'canvas') {
                this.canvas = true
            }
        },
        changeList: function (e) {

            if (e.target.classList.contains('li-hover')) {
                e.target.classList.remove('class', 'li-hover')
            } else{
                let arr = document.getElementsByClassName('list-item')
                for(let i = 0; i < arr.length; i++) {
                    arr[i].classList.remove('li-hover')
                }
                e.target.classList.add('class', 'li-hover')   
            }  
        },
        changePict: function () {
            let inner = document.getElementsByClassName('inner')[0];
            let reg=/\d{1,}/;
            let num = inner.style.transform.toString().match(reg)[0];
            num = parseInt(num) + 60;
            inner.style.transform = 'rotateY(' + num + 'deg)';
        }

    }
}
</script>

