import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

import Note from './components/notes/Notes.vue'
import Closure from './components/notes/Closure.vue'
import Prototype from './components/notes/Prototype.vue'
import EventLoop from './components/notes/EventLoop.vue'
import ToolFunction from './components/notes/ToolFunction.vue'
import Optimization from './components/notes/Optimization.vue'

import About from './components/About.vue'

import Demo from './components/demo/Demo.vue'
import Layout from './components/demo/Layout.vue'
import Animation from './components/demo/Animation.vue'


Vue.use(VueRouter)

Vue.config.productionTip = false

const routes = [
  { path: '/', redirect: '/about' },
  { path: '/notes', redirect: 'notes/closure' },
  { path: '/demo', redirect: 'demo/animation' },

  { path: '/about', component: About },
  { path: '/demo', component: Demo, children: [
    { path: 'layout', component: Layout },
    { path: 'animation', component: Animation },
  ]},
  { path: '/notes', component: Note, children: [
    { path: 'closure', component: Closure },
    { path: 'prototype', component: Prototype },
    { path: 'eventloop', component: EventLoop },
    { path: 'toolfunction', component: ToolFunction },
    { path: 'optimization', component: Optimization },
  ] }
]

const router = new VueRouter({
  routes
})

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
