<template>
    <div>
        <div id="left">
            <router-link to="/demo/layout" class="list" id="layout" >布局</router-link>
            <router-link to="/demo/animation" class="list" id="animation">动画</router-link>
        </div>
        
        <div id="right">
            <router-view></router-view>
        </div>
    </div>
</template>

<script>
// import Layout from './Layout.vue'

export default {
    name: 'Demo',
    mounted(){
        const cur = this.$route.path.split('/')[2]
        document.getElementById(cur).setAttribute('style', 'color: green;')
    },
    watch: {
        '$route'() {
            const cur = this.$route.path.split('/')[2]
            const links = document.getElementsByClassName('list')
            for (let i = 0; i < links.length; i++) {
                links[i].setAttribute('style', 'color: #2c3e50;')
            }
            document.getElementById(cur).setAttribute('style', 'color: green;')
            
        }
    }
}
</script>

