<template>
    <div>
        <div id="left">
            <router-link to="/notes/optimization" class="list" id="optimization">CSS优化</router-link>
            <router-link to="/notes/eventloop" class="list" id="eventloop">异步和事件循环</router-link>
            <router-link to="/notes/closure" class="list" id="closure">变量和闭包</router-link>
            <router-link to="/notes/prototype" class="list" id="prototype">原型和this</router-link>
            <router-link to="/notes/toolfunction" class="list" id="toolfunction">工具函数</router-link>
            
        </div>
        <div id="content">
             <router-view></router-view>
        </div>
    </div>
</template>

<script>


export default {
    name: 'Notes',
    mounted(){
        const cur = this.$route.path.split('/')[2]
        document.getElementById(cur).setAttribute('style', 'color: green;')
    },
    watch: {
        '$route'() {
            const cur = this.$route.path.split('/')[2]
            const links = document.getElementsByClassName('list')
            for (let i = 0; i < links.length; i++) {
                links[i].setAttribute('style', 'color: #2c3e50;')
            }
            document.getElementById(cur).setAttribute('style', 'color: green;')
            
        }
    }
}
</script>

<style scoped>
#left {
    /* background-color: antiquewhite; */
    position: absolute;
    top: 80px;
    bottom: 0;
    left: 0;
    width: 200px;
}

.list {
    padding: 10px;
    text-decoration: none;
    color: #2c3e50;
    display: block;
    text-align: center;
}

.list:hover {
    color: green;
}

#content {
    position: relative;
    left: 210px;
