<template>
    <div>
        <h3>对象</h3>
        <ul>
            <li>普通对象</li>
            <li>函数对象：通过 new Funciton 创建, var F = new Funciton()</li>
        </ul>
        <h3>prototype</h3>
           <ul>
                <li>只有<strong>函数对象</strong>才有的属性</li>
                <li>函数对象的 Prototype 指向其原型对象，其原型对象是一个普通函数</li>
                <li>原型对象的 constructor 仍指向该函数：F.prototype.constructor === F //true</li>
                <li>将该函数作为原型可创造实例</li>
           </ul>
        <h3>__proto__</h3>
            <ul>
                <li>每个对象都有该属性</li>
                <li>某个实例的__proto__指向其构造函数的原型对象</li>
                <li>实例 var f1 = new F()</li>
                <li>f1.__proto__ === F.prototype //true</li>
            </ul>
        <h3>JS中的 this 指向</h3>
        <p>和往常文章不同的一个学习思路，<a href="https://github.com/mqyqingfeng/Blog/issues/7" target="_blank">参考链接这里，一定要看，很酷。</a></p>
        <p>一般常见的 this 指向大概是这样的：</p>
        <div class="code">
            <code>
            var a = 2
            var o = {
                a: 1,
                b: function() {
                    console.log(this.a)
                } 
            }
            o.b() // 1 this指向的是这个对象
            
            var ob = o.b
            ob() // 2 this指向全局
            </code>
        </div>
        <p>上面的参考链接从语言规范的角度来描述 this 是怎么工作，首先明确这样几个概念。</p>
        <ul>
            <li>同时引入<strong> MemberExpression </strong>这个概念，简单来说就是括号的左边部分。</li>
            <li><strong>Reference</strong> 类型，存在于规范中的抽象概念，目的是帮助更好理解 JS 的各类机制，在实际代码开发中不会用到它。</li>
            <li>Reference 类型中有三个值，<strong>1：base value；2：referenced name；3：strict reference</strong>。</li>
            <li>base 的值可以理解为当前的变量存在于何处，一般存在于对象中或者是 Environment Record 里，后者可以理解为是储存数据的一个环境。
                根据规范，base value 的值一定在 undefined, an Object, a Boolean, a String, a Symbol, a Number, or an Environment Record 中。</li>
            <li>name 的值就是变量的名称，是字符串类型。</li>
            <li>string 的值是布尔，应该对应是否开启严格模式。</li>
            <li>Reference 类型提供<strong> GetBase </strong>方法来获取 Reference 的 base 值。</li>
            <li><strong> IsPropertyReference </strong> 方法判断 base 值是否为一个对象。</li>
        </ul>
        <div class="code">
            <code>
            //举例
            foo（） // MemberExpression -> foo。

            var o = { a: 1 }
            var a_reference = {
                base: o,
                name: 'a',
                strict: false
            }
            </code>
        </div>
        <p>this 判断指向的流程如下：</p>
        <ol>
            <li>计算 MemberExpression 的值给 ref</li>
            <li>判断 ref 是否为 Reference 类型
                <ol>
                    <li class="sub-ol">如果 ref 是 Reference 类型，且 IsPropertyReference(ref) 返回 true，即 base 是一个对象，则 this = GetBase（ref）</li>
                    <li class="sub-ol">如果 ref 是 Reference 类型，且 IsPropertyReference(ref) 返回 falze，即 base 不是一个对象，则 this = <strong>ImplicitThisValue(ref)</strong>，该函数始终返回 undefined</li>
                </ol>
            </li>
            <li>如果 ref 不是 Reference 类型，则 this = undefined，非严格模式下指向全局</li>
        </ol>

        <p>上述流程还存在的一个问题：<strong>如何判断 MemberExpression 返回的值是否为 Reference 类型。</strong></p>     
        <p>不同的操作，如赋值、逻辑运算，查找规范中的详细描述，运算步骤出现为<strong> return a value of type Reference</strong> 或者是<strong> let rval be GetValue(rref) 。</strong>前者返回的就是 Reference 类型，后者因为用了 GetValue 方法，返回值，不是 Reference。</p>   
        <p>上文给出链接中有更详细的例子。</p>
    </div>
</template>

