<template>
    <div class="">
        <ul class="nav">
            <router-link to="/demo" class="link" id="demo">Demo</router-link>
            <router-link to="/notes" class="link" id="notes">Notes</router-link>
            <router-link to="/" class="link" id="about">About Me</router-link>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'Nav',
    mounted(){
        const cur = this.$route.path.split('/')[1]
        document.getElementById(cur).setAttribute('style', ' border-bottom: 2px solid green;')
    },
    watch: {
        '$route'() {
            const cur = this.$route.path.split('/')[1]
            const links = document.getElementsByClassName('link')
            for (let i = 0; i < links.length; i++) {
                links[i].setAttribute('style', 'border: none;')
            }
            document.getElementById(cur).setAttribute('style', ' border-bottom: 2px solid green;')
            
        }
    }
}
</script>

<style>


.nav {
    position: fixed;
    z-index: 99;
    /* display: inline-block; */
    width: 100%;
    background-color: white;
    margin-top: 0;
    padding-top: 20px;
}

ul {
    top: 0;
}

.link{
    color: #2c3e50;
    text-decoration: none;
    display: inline;
    padding: 0 20px 0 20px;
    font-size: 25px;
}

.link:hover {
    color: green;
    transition: all .2s;
}

</style>


