<template>
    <div>


        <select name="" id="select" @change="changeOption">
            <option value="cup" >圣杯三列</option>
            <option value="three"  >Flex三列</option>
            <option value="nine"  >Flex九格</option>
            <option value="center">居中合集</option>
        </select>

        <div>
            <div id="cup"  v-show="this.cup">
                <div id="cup-center">
                    <p>父元素</p>
                    <p>box-sizing: border-box;</p>
                    <p>padding: 0 200px 0 200px;</p>
                    <p>padding 腾出位置</p>
                </div>
                <div id="cup-left">
                    <p>margin-left: -100%;</p>
                    <p>//左边部分向上推</p>
                    <p> width: 200px;</p>
                    <p>float: left;</p>
                    <p>left: -200px; </p>
                    <p>// 到center的左边</p>
                    <p>position: relative;</p>
                </div>
                <div id="cup-right">
                    <p>position: relative;</p>
                    <p>left: 200px;</p>
                    <p>width: 180px;</p>
                    <p>margin-left: -200px; </p>
                    <p> //右边部分上移</p>
                    <p>float: left;</p>
                </div>
            </div>
            <div id="three"  v-show="this.three">
                <div id="a"></div>
                <div id="b"></div>
                <div id="c"></div>
            </div>
            <div id="nine"  v-show="this.nine">
                <div class="row">
                    <div class="item">
                        <p>父元素</p>
                        <p>display: flex;</p>
                        <p>flex-direction: column;</p>
                    </div>
                    <div class="item">
                        <p>行元素</p>
                        <p>display: flex;</p>
                        <p>flex: 1;</p>
                    </div>
                    <div class="item">
                        <p>行内项目元素</p>
                        <p>flex: 1</p>
                    </div>
                </div>
                <div class="row">
                    <div class="item"></div>
                    <div class="item"></div>
                    <div class="item"></div>
                </div>
                <div class="row">
                    <div class="item"></div>
                    <div class="item"></div>
                    <div class="item"></div>
                </div>
            </div>
            <div id="center" v-show="this.center">
                <h4>绝对定位 + 负 margin，要求知道子元素宽高</h4>
                <div class="p1">
                    position: relative;
                    
                    <div class="c1">position: absolute;
                        top: 50%;
                        left: 50%;
                        margin-top: -60px;
                        margin-left: -100px;
                    </div>
                </div>

                <h4>绝对定位 + transform</h4>
                <div class="p1">
                    position: relative;
                    <div class="c2">// 将 margin 替换为
                    transform: translate(-50%, -50%);
                    </div>
                </div>

                <h4>绝对定位 + margin: auto</h4>
                <div class="p1">
                    position: relative;
                    <div class="c3"> // top、left等值都设置为0
                        margin: auto;
                    </div>
                </div>

                <h4>利用伪类</h4>
                <p>利用 :after 或者 :before 创建空元素，设置高度占满父元素，利用两个相邻的子元素在设置了 display: inline-block;
   和 vertical-align: middle; 之后会垂直对齐的原理。</p>
                <div class="p2">
                    <div class="c4"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Layout',
    data() {
        return {
            cup: true,
            three: false,
            nine: false,
            center: false
        }
    },
    methods: {
        clear: function () {
            this.cup = false
            this.three = false
            this.nine = false
            this.center = false
        },
        changeOption: function () {
            let select = document.getElementById('select')
            let index = select.selectedIndex
            let value = select.options[index].value
            this.clear()
            if (value == 'cup') {
                this.cup = true
            } else if (value == 'three') {
                this.three = true
            } else if (value == 'nine') {
                this.nine = true
            } else if (value == 'center') {
                this.center = true
            }
 
        }
    }
}
</script>

