<template>
    <div>
        <div>
            <h3>事件</h3>
            <p>事件可以分为<strong>同步事件</strong>和<strong>异步事件</strong>。同步即按照顺序执行，谁先来谁先运行；异步则要等前置任务完全后再执行。</p>
            <h3>执行栈与消息队列</h3>
            <ul>
                <li>同步事件依次进入执行栈执行</li>
                <li>异步事件的回调结果进入消息队列中，当前执行栈为空时再将消息队列中的事件放入执行栈执行</li>
                <li>以上步骤循环的过程即事件循环</li>
            </ul>

            <h3>异步事件</h3>
            <p>消息队列中的任务还可以分为宏任务和微任务，同一次事件循环中，微任务执行总是先于宏任务</p>
            <h4>宏任务（Macro Task）</h4>
            <ul>
                <li>setInterval()</li>
                <li>setTimeout()</li>
            </ul>

            <h4>微任务（Micro Task）</h4>
            <ul>
                <li>Promise.then()</li>
                <li>Promise.catch()</li>
                <li>Promise.finally()</li>
            </ul>
            <p>微任务和宏任务可以解释，为什么下面这段代码先输出 p1 再输出 p1 in timeout</p>
            <div class="code">
                <code>
                var p1 = new Promise(function(resolve, reject){
                    setTimeout(function(){console.log('p1 in timeout')}, 0)
                    resolve('p1')
                })

                p1.then(v => {console.log(v)})
                </code>
            </div>

            <h3>Promise</h3>
            <p>异步编程的解决方案，<a href="http://es6.ruanyifeng.com/#docs/promise" target="_blank">具体参考这里</a>。</p>
            <p>个人理解的 Promise 是普通回调写法的一种包装。理解 Promise 主要理解这样几个概念。</p>
            <ul>
                <li>Promise 的三种状态：1 pending 进行中，2 fulfilled 成功， 3 rejected 失败</li>
                <li>Promise 构造函数接收一个函数参数，该函数有两个由引擎提供的函数参数： resolve 和 reject</li>
                <li>resolve 函数将 Promise 的状态从 pending 转为 fulfilled，异步操作成功时使用，可以将成功结果传递出去</li>
                <li>reject 函数将状态转为 rejected，异步操作失败时使用，将失败错误传递出去</li>
            </ul>

            <div class="code">
                <code>
                // 常见用法
                var p1 = new Promise(function(resolve, reject) {
                    // 异步操作获得数据
                    resolve(data)
                })
                p1.then(data => {
                    // 对返回的数据进行操作
                })
                </code>
            </div>

            
        </div>

    </div>
</template>

